
import { SetupContext } from 'vue'

type Props = {
  label: string
  selected: boolean
}

export default {
  props: {
    label: { type: String, required: true },
    selected: { type: Boolean, default: false }
  },
  setup(props: Props, ctx: SetupContext) {
    return {
      state: props,
      select: () => ctx.emit('select')
    }
  }
}
